.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;
    
    &.error {
        background-color: $rojo;
    }
    &.exito {
        background-color: $verde;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: $verde;

        th {
            color: $blanco;
            padding: 2rem;
        }
    }
    tbody {
        td {
            text-align: center;
            font-size: 1.8rem;
            border-left: 1px solid rgba(192, 192, 192, 0.4);
            border-bottom: 1px solid rgba(192, 192, 192, 0.4);
            
        }
    }
    .imagen-tabla {
        width: 15rem;
        margin: 0 auto;
    }
}

.imagen-small {
    width: 20rem;
}

